import { Component, OnInit } from '@angular/core';
import { SelectItem, Message } from 'primeng/primeng';
import { CustomProfileAnalysisBean } from "app/pages/tzb/custom/custom-information/mine-custom/custom-profile-analysis/bean/CustomProfileAnalysis.bean";
import { Router } from "@angular/router";
import { CusOperationHttpService } from "app/pages/tzb/custom/http/custom-operation/custom-operation.http.service";
import { CustomProfileDetailBean } from "app/pages/tzb/custom/custom-information/mine-custom/custom-profile-analysis/bean/customProfileDetail.bean";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import * as _ from 'lodash';
@Component({
  selector: 'app-custom-profile-analysis',
  templateUrl: './custom-profile-analysis.component.html',
  styleUrls: ['./custom-profile-analysis.component.scss'],
  providers: [CusOperationHttpService, Commfunc]
})
/**
 * 客户九宫格
 */
export class CustomProfileAnalysisComponent implements OnInit {

  month: SelectItem[] = [];//月份
  y_axis: SelectItem[] = [];//纵轴
  x_axis: SelectItem[] = [];//横轴
  selectedAll: any = [];
  checkAll: boolean = false;
  checkBox: any = [];
  customProfileAnalysisBean: CustomProfileAnalysisBean = new CustomProfileAnalysisBean();//我的客户九宫格分析bean
  customProfileDetailBean: CustomProfileDetailBean = new CustomProfileDetailBean();//我的客户九宫格分析bean
  //列表
  text_center: any = { "text-align": "center" };
  isTableShow: boolean = false;
  dataList: any = [];
  dataList1: any;
  dataTable: any;
  totalNum: number;
  x_Interval1: string;
  x_Interval2: string;
  y_Interval1: string;
  y_Interval2: string;
  display: boolean = false;
  addDisplay: boolean = false;
  nodeDisplay: boolean = false;
  dealDisplay: boolean = false;
  property: boolean = false;
  inValue: any;
  nodeInValue: any;
  dealInValue: any;
  keyValue: any;
  addUpHeader: any;
  kindDo: string;
  chooseData: any = [];
  chooseMonth: string = '';
  //提示信息
  msgs: Message[] = [];
  userform: FormGroup;
  x_axisT: any = [
    { label: '总FTP值', value: "总FTP值" },
    { label: '存款FTP', value: "存款FTP" },
    { label: '贷款FTP', value: "贷款FTP" },
  ];
  y_axisT: any = [
    { label: '存款FTP', value: "存款FTP" },
    { label: '贷款FTP', value: "贷款FTP" },
  ];

  constructor(
    public router: Router,
    public httpService: CusOperationHttpService,
    public commfunc: Commfunc,
    public fb: FormBuilder, ) {
  }
  //初始化表格pageSize、pageNum
  ngOnInit() {
    this.customProfileDetailBean.pageSize = 10;
    this.customProfileDetailBean.pageNum = 1;
    this.customProfileAnalysisBean.labelVertAxis = '总FTP值';
    this.customProfileAnalysisBean.labelHorAxis = '存款FTP';
    this.customProfileDetailBean.month = '';
    this.x_axis = _.clone(this.x_axisT)
    this.doSelectX();
    this.doSelectY();
  }
  //查询九宫格
  query(chooseMonth) {
    this.checkBox = [];
    if (chooseMonth == "") {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "请选择月份！" });
      return
    }
    this.customProfileDetailBean.month = chooseMonth;
    let ob = this.httpService.myCustNineCellAnalysisCollect(this.customProfileDetailBean);
    ob.subscribe(data => {
      this.dataList = data.myCustNineCellListCollect ? data.myCustNineCellListCollect : [];
      this.dataList.sort((a, b) => {
        return a.nineCellLabel > b.nineCellLabel
      })
      this.x_Interval1 = data.x_Interval1;
      this.x_Interval2 = data.x_Interval2;
      this.y_Interval1 = data.y_Interval1;
      this.y_Interval2 = data.y_Interval2;
      if (data.returnCode.code == 'success') {
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
  //点击标签
  detailInit(i, num) {
    this.isTableShow = true;
    if (num == 1) {
      this.customProfileDetailBean.pageNum = 1;
    }
    for (let item in this.customProfileAnalysisBean) {
      this.customProfileDetailBean[item] = this.customProfileAnalysisBean[item]
    }
    this.customProfileDetailBean.nineCellLabel = i;
    this.Custdetail();
  }
  //查询表格
  Custdetail() {
    let ob = this.httpService.myCustNineCellAnalysisDetail(this.customProfileDetailBean);
    ob.subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.dataTable = data.myCustNineCellListDetail;
        this.totalNum = data.totalNum;
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
  //全选按钮
  checkAllClick() {
    if (this.dataTable) {
      for (let i = 0; i < this.dataTable.length; i++) {
        if (this.checkAll == true) {
          this.dataTable[i].checkBox = true;
          this.chooseData.push(this.dataTable[i]);
        }
        else {
          this.chooseData = [];
          this.dataTable[i].checkBox = false;
        }
      }
    }
  }

  //单选按钮
  checkBoxClick(i, data) {
    if (data.checkBox) {
      this.chooseData.push(data)
    } else {
      this.chooseData.forEach((item, i) => {
        if (item == data) {
          this.chooseData.splice(i, 1)
        }
      });
    }
    if (this.chooseData.length == this.dataTable.length) {
      this.checkAll = true;
    } else {
      this.checkAll = false;
    }
  }
  //分页
  paginate(event) {
    this.customProfileDetailBean.pageSize = event.rows * 1;
    this.customProfileDetailBean.pageNum = event.page + 1;
    this.Custdetail();
  }
  //跳转到个人视图
  toCustView(data) {
    this.commfunc.setSessionData('custNo', data.custNo);
    this.router.navigate(['pages/custom-view']);
  }
  onHideClose() {
    document.body.style.overflow = 'auto';
  }
  //跳转到个人持有产品
  toCustProduct(data) {
    this.commfunc.setSessionData('custNo', data.custNo);
    this.router.navigate(['pages/custom-view/view-product-information/own-our-product']);
  }

  doSelectX() {
    this.y_axis = _.clone(this.y_axisT)
    let x = this.customProfileAnalysisBean.labelVertAxis;
    let ind: number;
    this.y_axis.forEach((item, i) => {
      if (item['value'] == x) {
        ind = i;
        return;
      }
    })
    if (ind) {
      this.y_axis.splice(ind, 1)
    }
    if (ind == 0) {
      this.y_axis.splice(ind, 1)
    }

  }

  doSelectY() {
    this.x_axis = _.clone(this.x_axisT)
    let y = this.customProfileAnalysisBean.labelHorAxis;
    let ind: number;
    this.x_axis.forEach((item, i) => {
      if (item['value'] == y) {
        ind = i;
        return;
      }
    })
    this.x_axis.splice(ind, 1)
  }

  //新建联系计划
  newPlan() {
    if (this.chooseData.length == 0) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '请选择客户！' });
      return
    }
    document.body.style.overflow = 'hidden';
    this.addUpHeader = '新建客户联系计划';
    this.dealDisplay = false;
    this.nodeDisplay = false;
    this.display = true;
    this.addDisplay = true;
    let arr = _.clone(this.chooseData)
    this.inValue = arr;
  }

  //新建联系计划-关闭模态框
  newPlanCall(event) {
    this.addDisplay = false;
    this.display = false;
    document.body.style.overflow = 'auto';
  }

  //加入群组
  joinGroup() {
    if (this.chooseData.length == 0) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '请选择客户！' });
      return
    }
    let arrData = _.clone(this.chooseData);
    var a = JSON.stringify(arrData);
    let param = {
      info: a
    }
    this.router.navigate(['pages/tzb/custom/custom-marketing/group/join-group', param]);
  }

  //发送短信
  sendMessage() {
    if (this.chooseData.length == 0) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '请选择客户！' });
      return
    }
    this.addUpHeader = '发送短信';
    this.dealDisplay = false;
    this.addDisplay = false;
    this.display = true;
    this.nodeDisplay = true;
    let arr = _.clone(this.chooseData)
    this.nodeInValue = arr;
  }

  //处理
  dealTask(data) {
    if (this.chooseData.length == 0) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '请选择客户！' });
      return
    }
    if (this.chooseData.length > 1) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '仅支持对单个客户进行新建联系记录！' });
      return
    }
    document.body.style.overflow = 'hidden';
    this.addUpHeader = '新建联系记录';
    this.nodeDisplay = false;
    this.addDisplay = false;
    this.display = true;
    this.dealDisplay = true;
    let arr = _.clone(this.chooseData)
    this.dealInValue = arr;
  }

  //处理回调
  dealCall() {
    this.dealDisplay = false;
    this.display = false;
    document.body.style.overflow = 'auto';
  }
}
