import { Component, OnInit } from '@angular/core';
import { SelectItem, Message } from 'primeng/primeng';
import { MyCustHoldProdQryBean } from './bean/myCustHoldProdQry.bean';
import { CusOperationHttpService } from '../../../http/custom-operation/custom-operation.http.service';
// import { ProdCondition, CondRelation, ProductType, ProdType, LoanTypes } from '../../../constant/custOperation.codeValue';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Router } from "@angular/router";
import * as _ from 'lodash';
import { CALENDAR_CN } from "app/@uisftech/common/constant/config";
import { tableMessage } from "app/@uisftech/common/constant/config";

@Component({
  selector: 'app-custom-hold-products',
  templateUrl: './custom-hold-products.component.html',
  styleUrls: ['./custom-hold-products.component.scss'],
  providers: [CusOperationHttpService, Commfunc],
})
/**
 * 客户持有产品
 */
export class CustomHoldProductsComponent implements OnInit {

  tabMesg: any = tableMessage;//暂无数据提示
  //我的客户产品
  num: string = '0';//默认选中第1个
  //产品
  isProduct: boolean = false;//产品显示初始化为false
  productsData: any = [];//表数据
  prodType: any = [];//产品码值
  custNumTotal: any;//产品合计字段
  dlyAvgDepAmtTotal: any;
  dlyAvgLoanAmtTotal: any;
  smthDisIncomeTotal: any;
  smthFoldIncomeTotal: any;
  lmthDistIncomeTotal: any;
  lmthFoldIncomeTotal: any;
  ytdDistGrwTotal: any;
  ytdFoldGrwTotal: any;
  prodCatalogTotal: any;
  totalNum: any;
  productDisplay: boolean = false;//产品分页初始化为false
  productTotalNum: number;
  productPage: any = {
    pageSize: 10,
    pageNum: 1
  }
  //负债
  isDept: boolean = false;//资产显示初始化为false
  deptsData: any = [];//表数据
  loanType: any = [];//贷款码值
  loanCustNumTotal: any;//资产合计字段
  loanAmtTotal: any;
  loanBalanceTotal: any;
  loanCustNumPerMonthTotal: any;
  loanAmtPerMonthTotal: any;
  loanPrincipalAmtRepayPerMonthTotal: any;
  loanBalanceGrowPerMonthTotal: any;
  loanCustNumCurrYearTotal: any;
  loanAmtCurrYearTotal: any;
  deptDisplay: boolean = false;//负债分页初始化为false
  deptTotalNum: number;
  deptPage: any = {
    pageSize: 10,
    pageNum: 1
  }
  //资产
  isAsset: boolean = false;//负债显示初始化为false
  assetsData: any = [];//表数据
  assetCustNumCurrMonthTotal: any;//资产合计字段
  assetBalanceCurrMonthTotal: any;
  assetAvgBalanceCurrMonthTotal: any;
  assetAvgBalanceGrowCurrMonthAvgTotal: any;
  assetCustNumCurrYearTotal: any;
  assetBalanceCurrYearTotal: any;
  assetAvgBalanceCurrYearTotal: any;
  assetAvgBalanceGrowCurrYearTotal: any;
  assetAvgBalanceGrowCurrYearAvgTotal: any;
  assetDisplay: boolean = false;//资产分页初始化为false
  assetTotalNum: number;
  assetPage: any = {
    pageSize: 10,
    pageNum: 1
  }
  //获取所有码值
  code: any;

  constructor(
    public httpService: CusOperationHttpService,
    public commfunc: Commfunc,
    public router: Router,
  ) {
    this.codeValues() //调用方法，获取全部码值
    //获取所需要的码值,中括号中的变量是自己在原来码值表中的变量名！！！！
    this.prodType = this.code['ProdType'];
    this.loanType = this.code['LoanTypes'];
  }

  //初始化
  ngOnInit() {
    this.myCustDeptInit();//初始化客户负债分布
    this.myCustAssetInit();//初始化资产客户分布
  }

  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  //负债
  myCustDeptInit() {
    this.deptPage.pageSize = 10;
    this.deptPage.pageNum = 1;
    this.dept(this.deptPage);
  }
  dept(param) {
    let ob3 = this.httpService.myCustManageDebtDistributionQry(param);
    ob3.subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.deptsData = data.debtDistributionList;//表数据
        this.loanCustNumTotal = data.loanCustNumTotal;//合计字段
        this.loanAmtTotal = data.loanAmtTotal;
        this.loanBalanceTotal = data.loanBalanceTotal;
        this.loanCustNumPerMonthTotal = data.loanCustNumPerMonthTotal;
        this.loanAmtPerMonthTotal = data.loanAmtPerMonthTotal;
        this.loanPrincipalAmtRepayPerMonthTotal = data.loanPrincipalAmtRepayPerMonthTotal;
        this.loanBalanceGrowPerMonthTotal = data.loanBalanceGrowPerMonthTotal;
        this.loanCustNumCurrYearTotal = data.loanCustNumCurrYearTotal;
        this.loanAmtCurrYearTotal = data.loanAmtCurrYearTotal;
        this.deptTotalNum = data.totalNum;
        if (this.deptsData.length > 0) {
          this.isDept = true;
        } else {
          this.isDept = false;
        }
      }
    })
  }
  //资产
  myCustAssetInit() {
    this.assetPage.pageSize = 10;
    this.assetPage.pageNum = 1;
    this.asset(this.assetPage);
  }
  asset(param) {
    let ob3 = this.httpService.myCustManageAssetDistributionQry(param);
    ob3.subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.assetsData = data.assetDistributionList;//表数据
        this.assetCustNumCurrMonthTotal = data.assetCustNumCurrMonthTotal;//合计字段
        this.assetBalanceCurrMonthTotal = data.assetBalanceCurrMonthTotal;
        this.assetAvgBalanceCurrMonthTotal = data.assetAvgBalanceCurrMonthTotal;
        this.assetAvgBalanceGrowCurrMonthAvgTotal = data.assetAvgBalanceGrowCurrMonthAvgTotal;
        this.assetCustNumCurrYearTotal = data.assetCustNumCurrYearTotal;
        this.assetBalanceCurrYearTotal = data.assetBalanceCurrYearTotal;
        this.assetAvgBalanceCurrYearTotal = data.assetAvgBalanceCurrYearTotal;
        this.assetAvgBalanceGrowCurrYearTotal = data.assetAvgBalanceGrowCurrYearTotal;
        this.assetAvgBalanceGrowCurrYearAvgTotal = data.assetAvgBalanceGrowCurrYearAvgTotal;
        this.assetTotalNum = data.totalNum;
        if (this.assetsData.length > 0) {
          this.isAsset = true;
        } else {
          this.isAsset = false;
        }
      }
    })
  }

  //分页
  paginate(event, data) {
    // if (data == 'pro') {
    //   this.productPage.pageSize = event.rows * 1;
    //   this.productPage.pageNum = event.page + 1;
    //   this.product(this.productPage);
    // }
    if (data == 'dept') {
      this.deptPage.pageSize = event.rows * 1;
      this.deptPage.pageNum = event.page + 1;
      this.dept(this.deptPage);
    }
    if (data == 'asset') {
      this.assetPage.pageSize = event.rows * 1;
      this.assetPage.pageNum = event.page + 1;
      this.asset(this.assetPage);
    }
  }
  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }

}
