import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
//页面
import { MineCustomOverview2Component } from '../../custom-information/mine-custom/mine-custom-overview2/mine-custom-overview2.component';//我的客户概览2
import { MineCustomQueryComponent } from '../../custom-information/mine-custom/mine-custom-query/mine-custom-query.component';//我的客户查询
import { CustomProfileAnalysisComponent } from '../../custom-information/mine-custom/custom-profile-analysis/custom-profile-analysis.component';//客户九宫格分析
import { CustomAssetsLiabilityComponent } from '../../custom-information/mine-custom/custom-assets-liability/custom-assets-liability.component';//客户资产负债变动
import { CustomHoldProductsComponent } from '../../custom-information/mine-custom/custom-hold-products/custom-hold-products.component';//客户持有产品
import { CrossSellingComponent } from "../../../custom/cross-selling/cross-selling.component";//模型超市
export const routes: Routes = [
        { path: '', component: MineCustomOverview2Component },
        { path: 'mine-custom-overview2', component: MineCustomOverview2Component, },
        { path: 'mine-custom-query', component: MineCustomQueryComponent },
        { path: 'custom-Assets-liability', component: CustomAssetsLiabilityComponent },
        { path: 'custom-hold-products', component: CustomHoldProductsComponent },
        { path: 'integral-info-query', loadChildren:'./integral-info-query/integral-info-query.module#IntegralInfoQueryModule' },
        { path: 'cross-selling', component: CrossSellingComponent }
]

export const MineCustomRouting: ModuleWithProviders = RouterModule.forChild(routes);