import { Component, OnInit } from '@angular/core';
import { MineCustomQueryBean } from './bean/MineCustomQueryBean.bean';
import { CusOperationHttpService } from '../../../http/custom-operation/custom-operation.http.service';
// import { CERT_TYPE, Period, CustAttr } from "app/pages/tzb/custom/constant/custOperation.codeValue";
import { Message } from 'primeng/primeng';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Router } from '@angular/router'
@Component({
  selector: 'app-mine-custom-query',
  templateUrl: './mine-custom-query.component.html',
  styleUrls: ['./mine-custom-query.component.scss'],
  providers: [CusOperationHttpService, Commfunc]
})
/**
 * 我的客户查询
 */
export class MineCustomQueryComponent implements OnInit {
  mineCustomQueryBean: MineCustomQueryBean = new MineCustomQueryBean();//客户查询bean
  cert_type: any = [];//证件类型码值
  period: any = [];//客户类型码值
  custAttr: any = [];//客户属性码值
  dataTable: any = [];//数据
  totalNum: number;//总条数
  selectedAll: any = [];
  checkAll: boolean = false;
  checkBox: any = [];
  queryType: any;
  ss: string = 'none';
  //提示信息
  msgs: Message[] = [];
  //获取所有码值
  code: any;
  constructor(
    public httpService: CusOperationHttpService,
    public router: Router,
    public commfunc: Commfunc
  ) {
    this.codeValues() //调用方法，获取全部码值
    //获取所需要的码值,中括号中的变量是自己在原来码值表中的变量名！！！！
    this.cert_type = this.code['CERT_TYPE'];
    this.period = this.code['Period'];
    this.custAttr = this.code['CustAttr'];
  }

  ngOnInit() {
    this.mineCustomQueryBean.pageSize = 10;
    this.mineCustomQueryBean.pageNum = 1;
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  // 精确查询
  custDimQuery() {
    let ob = this.httpService.myCustQry(this.mineCustomQueryBean);
    ob.subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.dataTable = data.infoList;
        this.totalNum = data.totalNum;
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      }
      else {
        this.dataTable = data.infoList;
        this.totalNum = data.totalNum;
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }

  //全选按钮
  checkAllClick() {
    for (let i = 0; i < this.dataTable.length; i++) {
      this.selectedAll.push(String(i));
    }
    if (this.checkAll == true) {
      this.checkBox = this.selectedAll;
    } else {
      this.checkBox = [];
    }
  }

  //单选按钮
  checkBoxClick() {
    if (this.checkBox.length == this.dataTable.length) {
      this.checkAll = true;
    } else {
      this.checkAll = false;
    }
  }
  //分页
  paginate(event) {
    this.mineCustomQueryBean.pageSize = event.rows * 1;
    let currentPage = event.page + 1;
    this.mineCustomQueryBean.pageNum = currentPage;
    this.custDimQuery();
  }
  //跳转到个人视图
  toCustView(data) {
    this.commfunc.setSessionData('custNo', data.custNo);
    this.router.navigate(['pages/custom-view']);
  }

  compileStr(cust){
		return this.commfunc.compileStr(cust)
	  }
}
