import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { CrossSellingBean } from './bean/cross-selling.bean';
import { ConfirmationService } from 'primeng/primeng';
import { Message, SelectItem } from 'primeng/primeng';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { DomSanitizer } from '@angular/platform-browser';
import { tableMessage } from 'app/@uisftech/common/constant/config';


import { Validators, FormControl, AbstractControl, FormGroup, FormBuilder } from '@angular/forms';
import { CusOperationHttpService } from "app/pages/tzb/custom/http/custom-operation/custom-operation.http.service";
@Component({
    selector: 'cross-selling',
    styleUrls: ['cross-selling.component.scss'],
    templateUrl: 'cross-selling.component.html',
    providers: [ConfirmationService, Commfunc, CusOperationHttpService]
})

export class CrossSellingComponent implements OnInit {
    display: boolean = false;
    headerTitle: string;
    showModel: string;
    prodNum: string;
    radio: boolean = true;
    // 表格中显示的信息
    tabMesg: any = tableMessage;
    // 表体内容
    tableData: any;
    crossSellingBean: CrossSellingBean = new CrossSellingBean()
    //总页数
    total: string;
    //每页多少条
    pageSize: number = 10;

    //提示信息
    msgs: Message[] = [];
    productNum: any = [];

    option: any;//特征变量参数
    echartsDisplay: boolean = false;

    constructor(
        public cusOperationHttpService: CusOperationHttpService,
        public confirmationService: ConfirmationService,
    ) {

    }

    ngOnInit() {
        this.crossSellingBean.pageSize = 10;
        this.crossSellingBean.pageNum = 1;
        this.queryClick();
        this.queryPro();
    }


    //查询
    queryClick() {
        this.cusOperationHttpService.queryCrossSellingCustList(this.crossSellingBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tableData = data.custList;
                this.total = data.totalCount;
                // this.msgs = [];
                // this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })
    }

    //查询产品编码
    queryPro() {
        let param = {}
        this.cusOperationHttpService.queryCrossSellingProdList(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                data.prodList.forEach(element => {
                    let arr = {};
                    arr['label'] = element.prodName;
                    arr['value'] = element.prodNum;
                    this.productNum.push(arr)
                });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })
    }

    //分页
    paginate(event) {
        //每页显示的条数
        this.crossSellingBean.pageSize = event.rows;
        //当前页
        let currentPage = event.page + 1;
        this.crossSellingBean.pageNum = currentPage;
        //调用查询的方法
        this.queryClick();
        this.crossSellingBean.pageNum = 1;
    }

    // 重置
    reset() {
        this.crossSellingBean.custId = '';
        this.crossSellingBean.custName = '';
        this.prodNum = '';
        this.crossSellingBean.prodNum = ''
    }
    // selectProduct(){
    //     this.headerTitle = '选择产品';
    //     this.display = true;
    //     this.showModel = '1';
    // }

    // selectProductCall(param) {
    //     this.display = false;
    //     this.prodNum=param[0].productName
    //     this.crossSellingBean.prodNum=param[0].productCategoryId;
    // }

}
